import { useState, useEffect, useContext, useCallback } from "react";
import RateChart from "@/Components/GraphAndChart/RateChart";
import Template from "@/Components/GraphAndChartTemp/Template";
import { getTemplateData } from "@/Pages/Elderlies/Components/Utiles/utiles";
import { CustomContext } from "@/Context/UseCustomContext";
import dayjs from "dayjs";
import { getHeartRate } from "@/api/deviceReports";
import SkeletonRateChart from "@/components/Skeleton/SkeletonRateChart";

export default function HeartRate({ isSummaryBtn = true, dataType = "Week" }) {
  const context = useContext(CustomContext);
  const [chartData, setChartData] = useState([]);
  const [loading, setLoading] = useState(true); // Add loading state
  const {
    chartToDate = "",
    chartFromDate = "",
    elderlyDetails = {},
  } = context || {};
  const [toDate, setToDate] = useState(
    dayjs().subtract(1, "day").format("YYYY-MM-DD")
  );
  const [fromDate, setFromDate] = useState(
    dayjs().subtract(7, "day").format("YYYY-MM-DD")
  );
  const { title, color, icon, dataAnalysis, summaryProps } =
    getTemplateData("Heart Rate");

  const getHeartRateData = useCallback(() => {
    if (!elderlyDetails?.deviceId || !elderlyDetails._id) {
      setLoading(false);
      return;
    }
    setLoading(true);
    getHeartRate({
      // uids: "992300030457",
      uids: elderlyDetails?.bedRoomIds,
      elderly_id: elderlyDetails._id,
      to_date: fromDate,
      from_date: toDate,
    })
      .then((res) => {
        const data = res.data;
        console.log(data);
        const transformedData = {
          xdata: data?.map((item) => item.date),
          maxArray: data?.map((item) => item.max),
          minArray: data?.map((item) => item.min),
          allAvgNumber: Math.round(
            data?.reduce((sum, item) => sum + item.avg, 0) / data.length
          ),
        };
        console.log(transformedData);

        setChartData(transformedData);
        setLoading(false);
      })
      .catch((err) => {
        console.log(err);
        setLoading(false);
      });
  }, [toDate, fromDate, elderlyDetails]);

  useEffect(() => {
    getHeartRateData();
  }, [getHeartRateData]);

  useEffect(() => {
    if (chartToDate && chartFromDate) {
      setToDate(chartToDate);
      setFromDate(chartFromDate);
    }
  }, [chartToDate, chartFromDate]);

  return (
    <Template
      title={chartToDate ? "Heart Rate" : "Heart Rate (Weekly)"}
      color={color}
      icon={icon}
      dataAnalysis={dataAnalysis}
      isSummaryBtn={isSummaryBtn}
      summaryProps={summaryProps}
    >
      {loading ? (
        <SkeletonRateChart
          height={300}
          chartHeight={250}
          barCount={7}
          barWidth={40}
          gridLines={5}
        />
      ) : (
        <RateChart
          dateType={dataType}
          color={color}
          cheartInfo={chartData}
          fromDate={fromDate}
          toDate={toDate}
        />
      )}
    </Template>
  );
}
